'use client';

import TerritoryMap from '@/components/territoryMap';
import { useState } from 'react';

export default function Tabs() {
    const [activeTab, setActiveTab] = useState('tab1');
    const tabs = [
        { id: 'canvassing', label: 'Door Knocking' },
        { id: 'leads', label: 'Leads List' },
        { id: 'inspections', label: 'Inspections' },
    ];

    return (
        <div className="w-full max-w-4xl mx-auto mt-8">
            {/* Tabs Navigation */}
            {tabs.map((tab) => (
                <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`px-4 py-2 mr-2 rounded-t
                         ${activeTab === tab.id ? 'bg-blue-500 text-white' : 'bg-gray-200 hover:bg-gray-300 text-gray-700'}
                         `}
                >
                    {tab.label}
                </button>
            ))}
            {/* Tabs Content */}
            <div className="border border-gray-200 p-4 bg-white">
                {activeTab === 'canvassing' && (
                    <div>
                        <h2 className="text-xl font-bold mb-4">Door Knocking / Territory Management</h2>
                        <p>Click on the map to drop a pin and add a lead.</p>
                        <TerritoryMap />
                    </div>
                )}
                {activeTab === 'leads' && (
                    <div>
                        <h2 className="text-xl font-bold mb-4">Leads List</h2>
                        <p>Two left hand tabs here. 1 for new leads, 1 for existing leads. Existing leads are filtered by contact status and there is a search bar.</p>
                    </div>
                )}
                {activeTab === 'inspections' && (
                    <div>
                        <h2 className="text-xl font-bold mb-4">Inspections</h2>
                        <p>Calendar with a list of completed inspections and scheduled inspections.</p>
                    </div>
                )}
            </div>
        </div>
    );
} 